div {
  font-family: raleway;
  .home-wrapper {
    width: 70%;
    background-color: none;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }
  main {
    opacity: 0.63;
    font-size: 2rem;
    font-weight: bolder;
    text-align: center;
    padding: 6vh 15px;
    width: 60%;
    background: rgba(196, 196, 196, 0.5);
    border: 3px solid #ffffff;
    box-sizing: border-box;
    box-shadow: 0px 4px 4px #30b3a8;
    border-radius: 23px;
  }
  a {
    font-size: x-large;
    font-weight: bold;
    background: linear-gradient(
      180deg,
      #30b3a8 0%,
      rgba(255, 255, 255, 0) 100%
    );
    opacity: 0.7;
    margin-top: 60px;
    box-shadow: 0px 4px 14px #ffffff;
    border-radius: 36px;
    font-family: raleway;
    text-decoration: none;
    color: black;
    padding: 10px 20px;
    &:hover {
      border: 3px solid white;
    }
  }
}
