.loader{
    margin:0;
    padding:0;
    position:relative;
    width:100%;
    height:200px;
    overflow:hidden;
    border:1px solid black;
 
    svg{
        position:absolute;
        bottom:0;
        transform:rotateZ(30deg);
        animation:takeoff 4s infinite ease-in;
    }
}
  
  @keyframes takeoff{
    from {
      left:0;
    }
    35%{
      transform:rotateZ(30deg);
      bottom:0;
    }
    
    50%{
      transform:rotateZ(0deg);
    }
    66%{
      left:110%;
      bottom:80%;
    }
    to {
      left:100%;
    }
  }