.registerForm {
  width: 80%;
  margin: auto;
  padding: 20px 20px;
  background: rgba(196, 196, 196, 0.5);
  opacity: 0.85;
  border: 3px solid #ffffff;
  box-sizing: border-box;
  box-shadow: 0px 4px 4px #30b3a8;
  border-radius: 23px;
  form {
    display: flex;
    flex-direction: column;
    div {
      margin-top: 30px;
      &:last-child{
        align-self: flex-end;
      }
    }
    input,
    label {
      font-weight: 500;
      font-size: 1.2rem;
    }

    input {
      align-self: flex-end;
      font-family: Raleway;
      font-style: normal;
      padding-left: 12px;
      background: #ffffff;
      opacity: 0.7;
      border-radius: 11px;
      width: 25%;
      min-width: 120px;
      caret-color: #c7ab90;
    }
    .login {
      font-family: Raleway;
      font-style: normal;
      font-weight: 500;
      font-size: 1.4rem;
      margin-top: 30px;
      color: #ffffff;
      margin: 0 6px;
      height: var(--login-button-height);
      background: #000000;
      border-radius: 36px;
      padding: 0px 14px;
      outline: none;
      &:hover{
          box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
      }
    }
  }
}
